import React from 'react';
import PropTypes from 'prop-types';
import Step from '@material-ui/core/Step';
import Button from '@material-ui/core/Button';
import Stepper from '@material-ui/core/Stepper';
import StepLabel from '@material-ui/core/StepLabel';
import TextField from '@material-ui/core/TextField';
// import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    button: { marginRight: theme.spacing.unit },
    instructions: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
    },
    textField: {
        marginRight: "8px"
    },
    
});

function getSteps()
{
    return [ "Create Account" , "General Infomation" , "Update Profile Picture", "Payment Method" ];
}

class MainForm extends React.Component
{
    state = {
        activeStep: 0,
        name:"",
        skipped: new Set(),
    };

    isStepOptional = step => { return step === 1; };

    handleNext = () =>
    {
        const { activeStep } = this.state;
        let { skipped } = this.state;
        if (this.isStepSkipped(activeStep))
        {
            skipped = new Set(skipped.values());
            skipped.delete(activeStep);
        }
        this.setState({
            activeStep: activeStep + 1,
            skipped,
        });
        if( activeStep === getSteps().length - 1 ) { window.setTimeout( () => this.handleReset() , 5000 ); }
    };

    handleBack = () =>
    {
        const { activeStep } = this.state;
        this.setState({
            activeStep: activeStep - 1,
        });
    };

    handleSkip = () =>
    {
        const { activeStep } = this.state;
        if (!this.isStepOptional(activeStep))
        {
            // You probably want to guard against something like this,
            // it should never occur unless someone's actively trying to break something.
            throw new Error("You can't skip a step that isn't optional.");
        }

        this.setState(state =>
        {
            const skipped = new Set(state.skipped.values());
            skipped.add(activeStep);
            return {
                activeStep: state.activeStep + 1,
                skipped,
            };
        });
    };

    handleReset = () => {
        this.setState({
            activeStep: 0,
        });
    };

    isStepSkipped(step) {
        return this.state.skipped.has(step);
    }

    handleChange( event ) {
        this.setState({value: event.target.value});
    }

    switchState( payload ) {
        this.setState( payload );
    }

    render() {
        const { activeStep } = this.state;
        const { classes } = this.props;
        const steps = getSteps();
        const success_render = (
        <div className="success">
            <div>
                <h1> Congratulations </h1>
                <p> Now you’re one of us! </p>
                <i className="fas fa-smile fa-10x"></i>
            </div>
            <p className="helptext"> Redirected to homepage after 5 seconds… </p>
            {/* <Button hidden onClick={this.handleReset} className={classes.button}> Reset </Button> */}
        </div> );
        const all_steps = (payload) => [{
            title:"Create Account",
            intro:"Glad to see you here!",
            jsxdom:(<div className="create-account">
                <TextField id="account" label="account" margin="normal" />
                <TextField id="password" label="Password" type="password" margin="normal" />
                <TextField id="confirm-password" label="Confirm password" type="password" margin="normal" />
            </div>),
            regex:[]
        },{
            title:"General Infomation",
            intro:"Tell us who you are!",
            jsxdom:(<div className="general-infomation">
                    <div className="gap">
                        <TextField id="name" label="Name" margin="normal" />
                        <TextField id="phone" label="phone" margin="normal" />
                    </div>
                    <div className="birthday gap">
                        <TextField id="birthday-y" label="YYYY" type="number" margin="normal" />
                        <TextField id="birthday-m" label="MM" type="number" margin="normal" />
                        <TextField id="birthday-d" label="DD" type="number" margin="normal" />
                    </div>
                    <div className="address-area gap">
                        <TextField id="city" label="City" type="text" margin="normal" />
                        <TextField id="dist" label="Dist" type="text" margin="normal" />
                    </div>
                    <div className="gap">
                        <TextField id="address" label="Address Detail" type="text" margin="normal" />
                    </div>
                </div>),
            regex:[]
        },{
            title:"Update Profile Picture",
            intro:"We wanna know you more!",
            jsxdom:(<div>
                <div className="update-profile-picture">
                    <i className="fas fa-images fa-3x"></i>
                    <p>UPLOAD UP TO 3 PHOTOS <br /> MAX SIZE: 150*150px</p>
                </div>
            </div>),
            regex:[]
        },{
            title:"Payment Method",
            intro:"Add your credit card infomation!",
            jsxdom:(<div className="payment-method">
            <div className="gap">
                <TextField id="card-number" label="Card number" type="text" margin="normal"
                InputProps={{ endAdornment: ( <i className="fab fa-cc-visa"></i> ), }}
                />
            </div>
            <div className="gap">
                <TextField id="cardholder-name" label="Cardholder name" type="text" margin="normal" />
                <TextField id="bank-name" label="Bank name" type="text" margin="normal" />
            </div>
            <div className="gap">
                <TextField id="cvv" label="CVV" type="text" margin="normal" />
                <TextField id="expire-mm" label="MM" type="text" margin="normal" />
                <TextField id="expire-dd" label="DD" type="text" margin="normal" />
            </div>
            </div>),
            regex:[]
        },];

        return (
        <div className={classes.root}>
            <Stepper activeStep={activeStep}>
            { steps.map((label, index) => {
                const props = {};
                const labelProps = {};
                // if (this.isStepOptional(index)) {
                //     labelProps.optional = <Typography variant="caption">Optional</Typography>;
                // }
                if (this.isStepSkipped(index)) { props.completed = false; }
                return (
                <Step key={label} {...props} >
                    <StepLabel {...labelProps}></StepLabel>
                </Step>
                );
            })}
            </Stepper>
            <div>
            { this.state.activeStep === getSteps().length ? ( success_render ) :
                (
                <div>
                    <div className={classes.instructions}>
                        <h1> { all_steps(this)[activeStep].title } </h1>
                        <p> { all_steps(this)[activeStep].intro } </p>
                    </div>
                    <div id="vaildapp"> { all_steps(this)[activeStep].jsxdom } </div>
                    <div id="btn">
                        <Button
                        variant="contained"
                        color="primary"
                        onClick={ this.handleNext }
                        className={classes.button} >
                            { this === getSteps().length - 1 ? "DONE" : "SUBMIT & NEXT" }
                        </Button>
                    </div>
                </div>
                )
            }
            </div>
        </div>
    );
  }
}

MainForm.propTypes = {
    classes: PropTypes.object,
};

export default withStyles(styles)(MainForm);


